<style lang=less>
  @import '../../../assets/index.less';
  @import './maneyPwd.less';
</style>
<template>
  <div class="base-info maney-pwd">
    <!-- 设置资金密码 -->
    <p class="title">
      <span></span>{{$t('center.maneyPwd[0]')}}
    </p>
    <el-form  class="maney-pwd-form" :model="form">
      <el-form-item :label="$t('center.maneyPwd[1]')">
        <el-input v-model="form.moneyPwd" type="password" :placeholder="$t('center.maneyPwd[2]')"></el-input>
        <div class="maney-pwd-tel">{{isTelLogin ? $t('center.maneyPwd[3]') : $t('center.maneyPwd[4]')}}{{tel}}</div>
      </el-form-item>
      <div style="overflow: hidden;">
        <el-col :span="18">
        <el-form-item :label="$t('center.maneyPwd[5]')">
          <el-input v-model="form.code" :placeholder="$t('center.maneyPwd[6]')"></el-input>
        </el-form-item>
        </el-col>
        <el-col :span="6" class="codeBtn">
          <el-button type="primary" style="width: 112px;" :loading="getCodeLoading" :disabled="!ableGetCode" @click="getCodeRequest">{{getCodeText}}</el-button>
        </el-col>
      </div>
      <el-button class="submit" type="primary" :loading="submitLoading" @click="submit">{{$t('center.maneyPwd[7]')}}</el-button>
    </el-form>
  </div>
</template>
<script>
import {resetTpwd} from "@/api/user.js"
import {sendMsg,sendEmailMsg} from '@/api/public.js'
export default {
  data () {
    return {
      submitLoading:false,
      form:{//表单中数据
        moneyPwd:'',
        code:''
      },
      tel:'',
      countDownNumber:60,//倒计时秒数
      countDownTimer:null,//定时器
      getCodeText: this.$t('center.maneyPwd[8]'),//验证码处显示的内容
      ableGetCode:true,//验证码按钮能否点击
      sessionData:{},
      getCodeLoading:false,
      isTelLogin: this.$public.checkMobile(sessionStorage.getItem('mobile'))
    }
  },
  created () {
    this.sessionData = JSON.parse(sessionStorage.getItem('userInfo'))
    this.tel = this.$public.phoneEncryption(sessionStorage.getItem('mobile'))
  },
  watch: {
  },
  methods: {
    submit(){//提交
      if(!this.$public.pwd(this.form.moneyPwd)){
        this.$public.msg(this.$t('center.maneyPwd[9]'),'warning',this)
        return
      }

      if(!this.$public.code(this.form.code)){
        this.$public.msg(this.$t('center.maneyPwd[10]'),'warning',this)
        return
      }
      this.$public.googleVerify().then((r)=> {
        if(r){//验证通过
            this.submitRequest()
        } else{
            this.$public.msg(this.$t('center.maneyPwd[11]'),'error',this)
        }
      }).catch((r)=>{
        console.log('取消')
      })

    },
    submitRequest(){
      this.submitLoading = true
      let _data = {
        account: JSON.parse(sessionStorage.getItem('account')),
        tpwd: this.$public.Cryptographiccapital(this.form.moneyPwd),
        tpwd1: this.$public.Cryptographiccapital(this.form.moneyPwd),
        code: this.form.code,
        mobile: sessionStorage.getItem('mobile')
      }
      resetTpwd(_data).then((res)=>{
        this.submitLoading = false
        if(res.data.status == 200){
          this.$public.msg(this.$t('center.maneyPwd[12]'),'success',this)
          this.$router.back()
        } else{
          this.$public.msg(res.data.msg,'error',this)
          this.form.moneyPwd = ''
          this.form.code = ''
        }
      })
    },
    countDown(){//验证码倒计时
      this.ableGetCode = false
      let countDownCopy = this.countDownNumber
      this.countDownTimer = setInterval(()=>{
        if(this.countDownTimer && countDownCopy<=0){
          clearInterval(this.countDownTimer)
          this.getCodeText = this.$t('center.maneyPwd[13]')
          this.ableGetCode = true
          return
        }
        this.getCodeText = countDownCopy
        countDownCopy--;
      },1000)
    },
    getCodeRequest(){//发送获取验证码请求接口
      this.getCodeLoading = true
      this.ableGetCode = false
      if(this.isTelLogin){//手机登录下发送短信验证码
        let _data = {
          mobile: sessionStorage.getItem('mobile'),
          mcode: this.sessionData.mcode,
          type:4
        }
        sendMsg(_data).then((res)=>{
          this.getCodeLoading = false
          if(res.data.status == 200){
            this.$public.msg(this.$t('center.maneyPwd[14]'),'success',this)
            this.countDown()
          } else{
            this.$public.msg(res.data.msg,'error',this)
            this.ableGetCode = true
          }
        },
        ()=>{
          this.$public.msg(this.$t('center.maneyPwd[15]'),'error',this)
          this.getCodeLoading = false
          this.ableGetCode = true
        })
      } else{//发送邮箱验证码
        let _data = {
            email: sessionStorage.getItem('mobile')
        }
        sendEmailMsg(_data).then((res)=>{
          this.getCodeLoading = false
          if(res.data.status == 200){
            this.$public.msg(this.$t('center.maneyPwd[14]'),'success',this)
            this.countDown()
          } else{
            this.$public.msg(res.data.msg,'error',this)
            this.ableGetCode = true
          }
        },
        ()=>{
          this.$public.msg(this.$t('center.maneyPwd[15]'),'error',this)
          this.getCodeLoading = false
          this.ableGetCode = true
        })
      }
      
    }
  }
}
</script>



